<template>
    <div id="change-week">
        <Carousel />
        <div id="weekPanel" class="changeWeek">
            <ul class="collection with-header">
                <li class="collection-header center">
                    <h4>List Of Weeks</h4>
                </li>
                <a class="collection-item" v-for="(week, index) in listOfWeeks" :key="`week-${index}`" v-bind:class="{ active: index == active }"
                    v-on:click="changeActive(index), $emit('changed-week', listOfWeeks[index])">{{week.month}} / {{week.day}} / {{week.year}}</a>
            </ul>
        </div>
    </div>
</template>

<script>
import Carousel from "./Carousel";

export default {
    name: "ChangeWeek",
    components: {
        Carousel
    },
    data() {
        return {
            listOfWeeks: [],
            active: 0
        };
    },
    created: function() {
        // console.log("dfs: " + $("#thisWeekValue").val());
        // this.active = this.listOfWeeks.indexOf($("#thisWeekValue").val());

        $(".sidenav").sidenav("close");
        $(document).ready(function() {
            $(".carousel").carousel({
                fullWidth: true,
                indicators: true
            });
        });

        this.weekTestLoad();
    },
    methods: {
        weekTestLoad: function() {
            var week = {
                dateObj: new Date(),
                month: "09",
                day: "02",
                year: "18"
            };
            var week2 = {
                dateObj: new Date(),
                month: "09",
                day: "09",
                year: "18"
            };
            var week3 = {
                dateObj: new Date(),
                month: "09",
                day: "16",
                year: "18"
            };
            this.listOfWeeks.push(week);
            this.listOfWeeks.push(week2);
            this.listOfWeeks.push(week3);
        },
        changeActive: function(index) {
            this.active = index;
            let currentWeek = this.listOfWeeks[index].month + " / " + this.listOfWeeks[index].day + " / " + this.listOfWeeks[index].year;
        }
    }
};
</script>



